import React, { useState, useEffect } from "react";

import * as axios from "axios";
import User from "./User";



const Users = (props) => {
  let [usersOfVkontukte, setUsers] =useState([])
  function getUsers() {
    axios.get(`https://dry-headland-66248.herokuapp.com/api/Friends/${props.myUserId}`).then(response=>{
      setUsers(response.data.friends)
  
  })
  }
 
  

  let UsersOf = usersOfVkontukte.map((f) => (
    <User  myUserId= {props.myUserId} friends ={f.my_friends} />
  ));

  useEffect(() =>{getUsers()}, [props.userId])
  return (
    <div className="kj">
        {UsersOf}
    </div>
  );
};

export default Users;
