import React, { useState } from "react";
import { useEffect } from "react";
import "./User.css";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
const User = (props) => {
  let [src, setSrc] = useState("");
  let [id_go, setId_go] = useState("");
  let [my, setViewMy] = useState(true);
  function addFriend() {
    axios.post(
      `https://dry-headland-66248.herokuapp.com/api/Friends/${props.id}`,
      { userId: props.myUserId }
    );
    axios.post(
      `https://dry-headland-66248.herokuapp.com/api/Friends/${props.myUserId}`,
      { userId: props.id }
    );
  }
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    if (props.myUserId == props.id) {
      setId_go("ib" + props.myUserId);
      setViewMy(true);
    }
    if (props.myUserId != props.id) {
      setId_go("id" + props.id);
      setViewMy(false);
    }
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }
  let [viewFriends, setViewFriends] = useState([]);

  async function getFriendss() {
    await axios
      .get(`https://dry-headland-66248.herokuapp.com/api/friends/${props.id}`)
      .then(async (response) => {
        return await setViewFriends([...viewFriends, ...response.data.friends]);
      });
  }
  let [viewfinFriends, setViewFinFriends] = useState(false);

  let myFriends = viewFriends.map((f) => f.my_friends);

  function finViewFriends() {
    if (myFriends.includes(props.myUserId)) {
      setViewFinFriends(true);
    } else {
      setViewFinFriends(false);
    }
  }
  useEffect(() => {
    getFriendss();
  }, [props.id]);
  useEffect(() => {
    getFriendss();
  }, [addFriend]);
  useEffect(() => {
    fds();
  }, [props.id]);
  useEffect(() => {
    finViewFriends();
  }, [myFriends]);
  return (
    <div className="kjs">
      <div className="flex_user_about" id="flex_about_users_dop">
        <div className="alighn_huy">
          <NavLink to={id_go}>
            <img src={src} alt="" />
          </NavLink>
          <NavLink to={id_go}>
            <span>
              <span className="sp_toch_user1">
                {props.name + " " + props.surName}
              </span>
            </span>
          </NavLink>
        </div>
        {(!my && (
          <div className="text_of_flex_user_about_dop">
            {!viewfinFriends && (
              <span>
                <button className="sp_toch_user3" onClick={addFriend}>
                  Добавить в друзья
                </button>
              </span>
            )}
            {viewfinFriends && (
              <span>
                <button
                  id="have_friend_dop"
                  className="bt1_photo"
                >
                  У Вас в друзьях
                </button>
              </span>
            )}
          </div>
        )) || <div></div>}
      </div>
      <div className=" palka_search_between "></div>
    </div>
  );
};

export default User;
