import React, { useState } from "react";
import { useEffect } from "react";
import "./Profile_friends.css";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
const Friend = (props) => {
  let [src, setSrc] = useState("");
  let [id_go, setId_go] = useState("");

  let [name, setName] = useState("");

  function getNime(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setName(response.data.name);
      });
  }
 
  async function fds(e) {
      console.log(props.userId)
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.friends}`
    );
    getNime(props.friends);
    
    if (props.myUserId == props.id) {
      setId_go("ib" + props.myUserId);
    }
    if (props.myUserId != props.id) {
      setId_go("id" + props.friends);
    }
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }

  useEffect(() => {
    fds();
  }, [props.friends]);
  return (
    <div className="Wrapper_friend">
      <div className="flex_friend_about">
        <NavLink to={id_go}>
          <img src={src} alt="" />
        </NavLink>
        <NavLink to={id_go}>
          <span>
            <span className="sp_toch_friend"><span className='asvcs'><span>{name}</span></span></span>
          </span>
        </NavLink>
      </div>
    </div>
  );
};

export default Friend;
