import React, { useState } from "react";
import io from "socket.io-client";
import "../Chat.css";
import { useEffect } from "react";
import * as axios from "axios";
import { NavLink, withRouter } from "react-router-dom";

const socket = io.connect("https://frozen-scrubland-42194.herokuapp.com");

function Dialog(props) {
  let [src, setSrc] = useState("");
  let [id_go, setId_go] = useState("");

  let [name, setName] = useState("");
  let [surname, setSurname] = useState("");
  function getNime(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setName(response.data.name);
      });
  }
  function getSurName(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setSurname(response.data.surname);
      });
  }
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.userId}`
    );
    getNime(props.userId);
    getSurName(props.userId);
    if (props.myUserId == props.id) {
      setId_go("id" + props.match.params.userId);
    }
    if (props.myUserId != props.id) {
      setId_go("id" + props.match.params.userId);
    }
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }

  useEffect(() => {
    fds();
  }, [props.id]);

  const [state, setStaet] = useState({ message: "", name: "" });
  const [chat, setChat] = useState([]);

  useEffect(() => {
    socket.on("message", ({ name, message }) => {
      setChat([...chat, { name, message }]);
    });
  });

  const onTextChange = (e) => {
    setStaet({ ...state, [e.target.name]: e.target.value });
  };

  const onMessageSubmit = (e) => {
    e.preventDefault();
    const { message } = state;
    socket.emit("message", { name, message });
    setStaet({ message: "", name });
  };

  const renderChat = () => {
    return chat.map(({ name, message }, index) => (
      <div className="sender_about_sended" key={index}>
        <img src={src} alt="" />
        <div className="sender_about_sended_text">
          <span>{name}</span>
          <span>{message}</span>
        </div>
      </div>
    ));
  };

  return (
    <div className="wrapper_dialog">
      <div className="header_chat_dialog">
        <NavLink to="messages">
          <div className="flex_back_dialog">
            <img
              src="data:image/svg+xml;base64,"
              alt=""
            />
            <span>Назад</span>
          </div>
        </NavLink>
        <div className="middle_about_header_dialog">
          <NavLink to={id_go}>
            <span className="span_middle_header_name">
              {name + " " + surname}
            </span>
          </NavLink>
          <span className="span_middle_header_date">
            был(а) в сети 2 минуты назад{" "}
          </span>
        </div>
        <div className="end_settings_about_user_header_dialog">
          <img src={src} alt="" />
        </div>
      </div>
      <div className="wrapper_chat_main">
        <div>{renderChat()}</div>
        <form className="form_send_dialog" action="" onSubmit={onMessageSubmit}>
          <div>
            <input
              type="text"
              name="message"
              className="input_send_dialog"
              onChange={(e) => onTextChange(e)}
              value={state.message}
              label="Message"
              placeholder="Напишите сообщение..."
            />
          </div>
          <button>
            <img
              src="data:image/svg+xml;base64,"
              alt=""
            />
          </button>
        </form>
      </div>
    </div>
  );
}
let withRouterDialog = withRouter(Dialog);

export default withRouterDialog;
