import React, { useState, useEffect } from "react";
import * as axios from "axios";
import User from "./All_users/User";
import './All_users/User.css'
const Search_main = (props) => {
  let [usersOfVkontukte, setUsers] = useState([]);

  function getUsers() {
    axios
      .get("https://dry-headland-66248.herokuapp.com/api/users")
      .then((response) => {
        setUsers(response.data);
      });
  }

  let UsersOf = usersOfVkontukte.map((p) => (
    <User
      myUserId={props.userId}
      id={p._id}
      surName={p.surname}
      name={p.name}
    />
  ));

  useEffect(() => {
    getUsers();
  }, []);

  let [countFriend, setCountFriend] = useState([]);
  function getFriendss() {
    axios
      .get(
        `https://dry-headland-66248.herokuapp.com/api/users`
      )
      .then((response) => {
        setCountFriend(response.data.length);
      });
  }
  useEffect(() => {
    getFriendss();
  }, [countFriend]);

  if (props.isAuth == false) {
    return <Search_main to={"/auth"} />;
  }
  return (
    <section className="Friends">
      <div className="header_friends">
        <span className ='people_label_search'>Люди</span>
        <span className ='count_people_label_search'>{countFriend}</span>
      </div>
      {UsersOf}
    </section>
  );
};

export default Search_main;
