import React, { useState } from "react";

import './Chat.css'
import { useEffect } from "react";
import * as axios from "axios";
import UserForMessage from "./UserForMessage/UserForMessage";



function Chat(props) {
  let [usersOfVkontukte, setUsers] = useState([]);
  function getUsers() {
    axios
      .get("https://dry-headland-66248.herokuapp.com/api/users")
      .then((response) => {
        setUsers(response.data);
      });
  }

  let UsersOf = usersOfVkontukte.map((p) => (
    <UserForMessage
      myUserId={props.userId}
      id={p._id}
      surName={p.surname}
      name={p.name}
    />
  ));

  useEffect(() => {
    getUsers();
  }, []);




  
  
  return (
    <div className="wrapper_chat">
      <div className="header_chat_search">
        <input type="text" placeholder ='Поиск'/>
      </div>
      <div className="wrapper_chat_main">
        {UsersOf}

      </div>
   
    </div>
  );
}

export default Chat;
