import React, { useState, useEffect } from "react";
import "./Profile_friends.css";
import * as axios from "axios";
import Friend from "./Friend";

const Profile_friends = (props) => {
  let [countFriend, setCountFriend] = useState([]);
  function getFriendss() {
    axios
      .get(
        `https://dry-headland-66248.herokuapp.com/api/friends/${props.userId}`
      )
      .then((response) => {
        setCountFriend(response.data.friends.length);
      });
  }
  useEffect(() => {
    getFriendss();
  }, [props.userId]);




  let [usersOfVkontukte, setUsers] =useState([])
  function getUsers() {
    axios.get(`https://dry-headland-66248.herokuapp.com/api/Friends/${props.userId}`).then(response=>{
      setUsers(response.data.friends)
  
  })
  }
 
  

  let UsersOf = usersOfVkontukte.map((f) => (
    <Friend myUserId= {props.myUserId} friends ={f.my_friends} userId = {props.userId} />
  ));

  useEffect(() =>{getUsers()}, [props.userId])

  return (
    <div className="wrapper_profile_friends">
      <div className="top_friends_info">
        <div className="div">
          <span className="friend_label">Друзья</span>
          <span className="count_friends">{countFriend}</span>
        </div>
        <span className="updates_friends">
          <span>обновления</span>
        </span>
      </div>
      <div className="friends_main">
        {UsersOf}
      </div>
      <div className="palka_friends"></div>
      <div className="top_friends_info">
        <div className="div">
          <span className="friend_label">Друзья онлайн</span>
          <span className="count_friends">{countFriend}</span>
        </div>
      </div>
      <div className="friends_main">
      {UsersOf}
      </div>
    </div>
  );
};

export default Profile_friends;
