import React, { useState } from "react";
import { useEffect } from "react";
import "../Friends.css";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
const User = (props) => {
  let [src, setSrc] = useState("");
  let [id_go, setId_go] = useState('')

  let [name, setName] = useState("");
  let [surname, setSurname] = useState("");
  function getNime(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setName(response.data.name);
      });
  }
  function getSurName(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setSurname(response.data.surname);
      });
  }
  async function fds(e) {
      
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.friends}`
    );
    getNime(props.friends);
    getSurName(props.friends);
    if(props.myUserId == props.id){
        setId_go('ib' + props.myUserId)
    } 
    if (props.myUserId != props.id){
        setId_go('id' + props.friends)
    }
    if (photoId.data.photo == "") {
     
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
       
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }
  
  useEffect(() => {
    fds();
  }, [props.id]);
  return (
    <div className="kjs">
      <div className="flex_user_about">
        <NavLink to ={id_go}><img src={src} alt="" /></NavLink>
        <div className="text_of_flex_user_about">
          <NavLink to = {id_go} ><span><span className ='sp_toch_user1'>{name + " " + surname}</span></span></NavLink>
          <span><span className ='sp_toch_user2'>Написать сообщение</span></span>
        </div>
      </div>
    </div>
  );
};

export default User;
