import React, { useState, useEffect } from "react";
import "./Friends.css";
import Users from "./Users/Users";
import { Redirect } from "react-router-dom";
import * as axios from "axios";
const Friends_main = (props) => {
  let [countFriend, setCountFriend] = useState([]);
  function getFriendss() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/friends/${props.userId}`)
      .then((response) => {
        setCountFriend(response.data.friends.length);
      });
  }
  useEffect(() => {
    getFriendss();
  }, [props.userId]);
  if (props.isAuth == false) {
    return <Redirect to={"/auth"} />;
  }

  return (
    <section className="Friends">
      <div className="header_friends">
        <span className ='people_label_search'>Все друзья</span>
        <span className ='count_people_label_search'>{countFriend}</span>
      </div>
      <Users myUserId={props.userId}  userId = {props.userId}/>
    </section>
  );
};

export default Friends_main;
