import React, { useState } from "react";
import { useEffect } from "react";
import "../Chat.css";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
const UserForMessage = (props) => {
  let [src, setSrc] = useState("");
  let [name, setName] = useState("");
  let [id_go, setId_go] = useState("");
  let [surname, setSurname] = useState("");
  function getNime(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setName(response.data.name);
      });
  }
  function getSurName(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setSurname(response.data.surname);
      });
  }
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );

    getNime(props.id);
    getSurName(props.id);
    if (props.myUserId == props.id) {
      setId_go("dialog" + props.myUserId);
    }
    if (props.myUserId != props.id) {
      setId_go("dialog" + props.id);
    }
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }
  let [viewFriends, setViewFriends] = useState([]);

  async function getFriendss() {
    await axios
      .get(`https://dry-headland-66248.herokuapp.com/api/friends/${props.id}`)
      .then(async (response) => {
        return await setViewFriends([...viewFriends, ...response.data.friends]);
      });
  }
  let [viewfinFriends, setViewFinFriends] = useState(false);

  let myFriends = viewFriends.map((f) => f.my_friends);

  function finViewFriends() {
    if (myFriends.includes(props.myUserId)) {
      setViewFinFriends(true);
    } else {
      setViewFinFriends(false);
    }
  }
  useEffect(() => {
    getFriendss();
  }, [props.id]);

  useEffect(() => {
    fds();
  }, [props.id]);
  useEffect(() => {
    finViewFriends();
  }, [myFriends]);
  return (
    <NavLink to={id_go}>
      <div className="wrapper_user_chat">
        <div className="flex_about_user_chat">
          <img src={src} alt="" />
          <div className="flex_about_user_chat_text">
            <span className="span_about_user_chat">{name + " " + surname}</span>
            <span>мэссаж</span>
          </div>
        </div>
      </div>
    </NavLink>
  );
};

export default UserForMessage;
